<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>
    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('USERNAME') }}</label>
          <vs-input class="w-full" v-model="user.username"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <vs-checkbox v-model="user.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <vs-button class="btnx" type="filled" @click="modifyUser('browse')">{{ $t('SUBMIT') }}</vs-button>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import SecurityService from '@/services/nucleus/SecurityService'
export default {
  name: 'user-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      user: {}
    }
  },
  methods: {
    async load () {
      let self = this
      SecurityService.users({
        userId: this.$route.params.id
      }).then(function (response) {
        self.user = response.data.userList[0] || {}
        self.user.disabled = self.user.disabled === 'Y'
      })
    },
    modifyUser (nextAction) {
      let self = this
      SecurityService.modifyUser({
        userId: this.user.userId,
        username: this.user.username,
        disabled: this.user.disabled ? 'Y' : 'N'
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewUser', params: { id: response.data.userId } })
          } else {
            self.$router.push({ name: 'BrowseUsers' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
